function addProperty(prop, defObject, destination){
  let propId = prop.id;
  let propDef = defObject.find( (propDef)=>{ return propDef.id == propId } );
  //addPropertyDestination.value = destination;
  setNestedProperty(destination, propId, propDef.default);
    submitChanges();
}

function getNestedProperty(obj, propName) {
    const propParts = propName.split('.');
    for (let i = 0; i < propParts.length; i++) {
        if (!obj || !obj.hasOwnProperty(propParts[i])) {
            return undefined;
        }
        obj = obj[propParts[i]];
    }
    return obj;
}

function setNestedProperty(obj, propName, value) {
    const propParts = propName.split('.');
    for (let i = 0; i < propParts.length - 1; i++) {
        if (!obj.hasOwnProperty(propParts[i]) || typeof obj[propParts[i]] !== 'object') {
            obj[propParts[i]] = {};
        }
        obj = obj[propParts[i]];
    }
    obj[propParts[propParts.length - 1]] = value;
}








function submitChanges(){
  console.log( 'ChartProperties::submitChanges', selectedComponent.attributes.attributes );
  let newAttributes = {};
  let newData, newConfig, newLayout;
  
  if( attributeIsBinding('data') ){
    newData = selectedComponent.attributes.attributes.gb_b__data
    newAttributes.gb_b__data = newData;
  }else{
    newData = btoa( JSON.stringify(traces.value) );
    newAttributes.gb_c__data = newData;
  }
  
  if( attributeIsBinding('config') ){
    newConfig = selectedComponent.attributes.attributes.gb_b__config
    newAttributes.gb_b__config = newConfig;
  }else{
    newConfig = btoa( JSON.stringify(config.value) );
    newAttributes.gb_c__config = newConfig;
  }
  
  if( attributeIsBinding('layout') ){
    newLayout = selectedComponent.attributes.attributes.gb_b__layout
    newAttributes.gb_b__layout = newLayout;
  }else{
    newLayout = btoa( JSON.stringify(layout.value) );
    newAttributes.gb_c__layout = newLayout;
  }

  props.selectedComponent.setAttributes( newAttributes );

  props.selectedComponent.view.$el[0].vueApp.updateAttributes(newAttributes);
  //props.selectedComponent.view.$el[0].vueApp.updateAttributes({data:newData, config: newConfig, layout:newLayout });
  //props.selectedComponent.view.$el[0].vueApp.$forceUpdate();

}


  
function setSelectedComponent(newComponent){
 
  //let newComponent = props.selectedComponent;
    if( !newComponent || newComponent.attributes.type != 'plotly' ){
      traces.value = null;
      config.value = null;
      layout.value = null;
      return;
    }
    let componentAttributesRaw = newComponent.getAttributes();
    if( componentAttributesRaw.gb_c__data )
      componentAttributesRaw.gb_c__data = atob(componentAttributesRaw.gb_c__data);
    if( componentAttributesRaw.gb_c__config )
      componentAttributesRaw.gb_c__config = atob(componentAttributesRaw.gb_c__config);
    if( componentAttributesRaw.gb_c__layout )
      componentAttributesRaw.gb_c__layout = atob(componentAttributesRaw.gb_c__layout);
    

    let data = componentAttributesRaw.gb_c__data || "[]";
    data = JSON.parse(data);
    traces.value = data;

    let configInput = componentAttributesRaw.gb_c__config || "{}";
    configInput = JSON.parse(configInput);
    config.value = configInput;
    
    let layoutInput = componentAttributesRaw.gb_c__layout || "{}";
    layoutInput = JSON.parse(layoutInput);
    layout.value = layoutInput;
}

function findBindingValues(obj) {
  let result = [];

  function recurse(o) {
    for (let key in o) {
      if (typeof o[key] === 'object' && o[key] !== null) {
        recurse(o[key]);
      } else if (typeof o[key] === 'string' && o[key].startsWith('$_{') && o[key].endsWith('}')) {
        result.push({ value: o[key] });
      }
    }
  }

  recurse(obj);
  return result;
}

function addNewTrace(){
  traces.value.push( { x:'', y:'', type:'scatter', name:'Trace' } )
}

function addNewTraceFromBinding(columnA, columnB){
  traces.value.push(
    { x:columnA, y:columnB, type:'scatter', name:'Trace' }, 
  );
  submitChanges();

}
onBeforeMount( ()=>{
    setSelectedComponent( props.selectedComponent );
    getFieldsOfDataFrameType();
});

defineExpose({
    setSelectedComponent, addNewTraceFromBinding
})

